    width: 18px;
    height: 18px;
    background-color: white;
    border-radius: 4px;
    border: 1px solid #D0D0D0;
    vertical-align: middle;
    margin-right: 9px;
}


input[type='checkbox']:checked +label::before {
    content: '';
